*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;

}
.OuterDiv{
    color: aliceblue;
    height: 100vh;
    background-image: linear-gradient(rgb(82, 6, 137),rgb(148, 130, 238));
    display: flex;
    justify-content: center;
    align-items: center;
}
.InnerDiv{
    height: 70vh;
    width: 60vh;
    background-image: linear-gradient(rgb(12, 12, 34),rgb(9, 18, 34));
    display: flex;
    flex-direction: column;
    border-radius: 1.5rem;
    padding: 3rem 1rem;
    justify-content: space-evenly;
}
form{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    
}



.InnerDiv a{
    text-decoration: none;
    color:aliceblue;
}



input{
    margin: 0.5rem 0;
    width: 60%;
    box-sizing: border-box;
}
.ButtonLogin{
    display: block;
    padding: 0.5rem;
    width: 7rem;
    height: 3rem;
    margin : 1rem 0;
    text-align: center;
    /* border: 1rem 1rem ; */
    border-style: solid;
    border-color: aliceblue;
    font-weight: bold;
    
}

.Icons i{
    padding: 0.2rem 0.5rem;
}

@media screen and (max-width : 850px){
    .OuterDiv{
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .InnerDiv{
        height: 60vh;
        width: 45vh;
        justify-content: space-evenly;
    }
    
}