.destination{
    margin: 4rem 6rem;
}
.destination h1{
    font-size: 3rem;
}

.first-dest{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 4.5rem;
}
.first-dest-rev{
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    justify-content: space-between;
    margin-top: 4.5rem;
}

.text-dest{
    width: 45%;
    text-align: start;
    font-size: 1.1rem;
}
.text-dest h2{
    padding-bottom: 1rem;
}
.img-dest{
    position: relative;
    width: 50%;
    display: flex;
    justify-content: space-between;
    z-index: -99;

}
.img-dest img{
    width: 49%;
    height: 350px;
    object-fit: cover;
    border-radius: 6px;
    box-shadow: -1px 1px 62px -18px rgba(0, 0, 0, 0.19);
}
.img-dest img:nth-child(2){
    position: absolute;
    top: -10%;
    right: 0;
}

@media screen and (max-width : 850px) {
    .destination{
        margin: 4rem 2rem;
    }
    .first-dest{
        display: flex;
        flex-direction: column-reverse;
        margin-top: 2.5rem;
    }
    .first-dest-rev{
        display: flex;
        flex-direction: column-reverse;
        margin-top: 4.5rem;
    }
    .text-dest{
        width: 100%;
    }
    .img-dest{
        width: 100%;
        margin: 1.5rem 0;
    }
    .img-dest img{
        height: 250px;
    }
}