.aboutInfo{
    width: 100%;
    height: 100%;
}
.aboutInfo img{
    margin-top: 7rem;
    width: 100%;
    height: 45vh;
    position: relative;
    object-fit: cover;
}
.textInfo{
    border-radius: 1rem;
    background: white;
    position: relative;
    margin : -7rem 10rem 10rem 10rem ;
    box-shadow: 0.3rem 0.3rem 0.3rem 0.3rem rgba(0, 0, 0, 0.11);
    padding: 4rem 2rem;
    text-align: start;
    justify-content: center;
}
.textInfo h1{
    padding-bottom: 1rem;
    font-size: 1.8rem;
    color: rgba(red,blue,green,yellow 0.11);
}
.textInfo h3{
    padding: 1rem 0;
}
.textInfo ul{
    padding-top: 1rem;
    list-style-position: inside;;
}

@media screen and (max-width : 850px) {
    .textInfo{
        margin:-7rem 2rem 2rem 2rem;
    }

    
}