.serviceinfo{
  position: relative;
}
.card{
  border: 1px solid white;
  background: black;
  border-radius: 8px;
  overflow: hidden;
}
.left>img{
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.carousel-img{
  margin: auto;
  width: 800px;
}
.box{
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding: 20px;
}
.box>img{
  width: 100%;
  height: auto; /* Let the image adjust its height based on the aspect ratio */
  max-height: 400px;
  object-fit: cover;
}
.box p {
  margin-top: 10px;
}
@media screen and (max-width: 850px) {
  .carousel-img{
    width: auto;
  }
}