.hero{
    width: 100%;
    height: 100vh;
    position: relative;
}
img{
    width: 100%;
    height: 100%;
}
.Hero-text{
    color: white;
    position: absolute;
    top: 80%;
    left: 50%;
    transform: translate(-50% ,-50%);
    width: 100%;
}
.Hero-text h1{
    font-size: 3rem;
    font-weight: 800;
    background: white;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}
.Hero-text p {
    font-size: 1.2rem;
}
.Hero-text a{
    position: relative;
    top: 1rem;
    font-size: 1rem;
    font-weight: bold;
    text-decoration: none;
    background-color: white;
    padding: 0.5rem;
    border-radius: 6rem;
    color: black;
}

@media screen and (max-width : 850px) {
    .Hero-text h1{
        font-size: 1rem;
    }
    .Hero-text p{
        font-size: 0.7rem;
    }
    .Hero-text a{
        position: relative;
        top : 0.4rem;
        font-size: 0.4rem;
        font-weight: bold;
        text-decoration: none;
        background-color: white;
        border-radius: 6rem;
        color: black;
    }
    img{
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
    
}

/* ============= about ============== */
.heroAbout {
    height: 85vh;
    width: 100%;
    position: relative;
}
.heroAbout h1{
    background: linear-gradient(to left, rgb(234, 126, 234),
    indigo,
    rgb(35, 35, 251),
    green,
    yellow,
    orange,
    red);
    -webkit-background-clip: text;
    color: transparent;
}
.heroAbout p{
    font-weight: 600;
}
.heroAbout a{
    display: none;
}
@media screen and (max-width : 850px) {
    .heroAbout h1{
        font-size: 2rem;
    }    
    .heroAbout p{
        position: relative;
        font-size: 0.7rem;
    }
}




/* ==========Hero Service ===========*/


.heroService{
    width: 100%;
    height: 110%;
}

.heroService a{
    display: none;
}



/* ======= Contact Us ===========*/
.heroContact{
    width: 100%;
    height: 50vh;
    position: relative;
}
.heroContact a{
    display: none;
}