
.contact-form{
    display: flex;
    margin: 4rem 12rem;
    border-radius: .5rem;
    box-shadow:5px 5px 20px 5px rgba(0, 0, 0, 0.11);
    padding:5px;
    position: relative;
    justify-content: space-between;
    background-image: linear-gradient(rgb(116, 35, 230),rgb(21, 14, 161));

}
.contact-info{
    background-color: rgb(48, 48, 227);
    border-radius: .5rem;
    padding: 1rem 2rem;
    width: 40%; 
    position:relative;
    justify-content: flex-start;
    align-items: flex-start;
    margin : 1rem 1rem;
}
.contact-info h2{
    text-align: start;
    font-size: 1.7rem;
    font-weight: 500;
}
.contact-info p{
    text-align: start;
    padding-top: 0.6rem;
    font-size: medium;
    font-weight: 300;
    color :whitesmoke;
}
.logo{
    text-align: start;
    padding:2rem 0;
    display: flex;
    flex-direction: column;
}

.icons{
    color: rgb(250, 136, 55);
    margin-top: 2rem;
}
.icons i{
    margin-right: 1rem;
}
.icons span{
    color:white;
    text-decoration: none;
}
.social{
    display: flex;
    justify-content: space-between;
}

.social-media{
    width: 40%;
    display: flex;
    justify-content: space-around;
    margin-top: 4rem;
    margin-bottom: 1rem;
}
.social-media i{
    color: white
}
.social-media i:hover{
    background-image:linear-gradient(rgb(0, 166, 255) ,white);
    color: rgb(243, 122, 245);
    transition: 0.3s ease-in;
}
.Contact-form1{
    width: 55%;
    margin: 1rem ;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.MainLabel {
    display: flex;
    flex-direction: column;
    width: 300%;
    height: 70%;
}
label{
    display: flex;
    flex-direction: flex-start;
}
input{
    display: flex;
    left: 3rem;    
}

.textarea{
    height: 6rem;
    padding:0.5rem;
    margin-bottom: 2rem;
}
.ButtonLogin-btn{
    display: block;
    align-content: center;
    padding: 0.5rem;
    width: 6rem;
    height: 3rem;
    margin : 1rem 0; 
    text-align: center;
    /* border: 1rem 1rem ; */
    border-style: solid;
    border-color: aliceblue; 
    font-weight: bold;
    color:white ;
    text-decoration: none;
}

@media screen and (max-width: 850px) {
    .contact-form{
        margin: 5rem 7rem;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
    .contact-info{
        margin: 1rem 10rem;
        width: 80%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-self: center;
        
    }
    
    .social-media{
        margin-left: 2rem;
        gap : 2rem;
    }
    .contact-form1{
        width: 100%;
        display : flex;
        align-items: center;
        justify-content: center;
    }
    .MainLabel{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
    
    
}